.button-purple {
  padding: 5px 20px;
  border: none;
  border-radius: 20px;
  background-color: $buttonsBackgrondColors;
  color: $fontButtonsColors;
  font-weight: 700;
  transition: all 0.4s;
  &:hover {
    background-color: $buttonsActiveBackgrondColors;
    transform: scale(1.03);
  }
}
