* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.modal {
  position: fixed;
  display: none;
  inset: 0;
  z-index: 999;
  background-color: rgba(18, 18, 18, 0.5);
}
.modal--active {
  display: block;
}
.modal__clicks {
  font-weight: 700;
}
.modal__heading {
  font-size: 42px;
}
@media (max-width: 600px) {
  .modal__heading {
    font-size: 32px;
  }
}
.modal__description {
  font-size: 16px;
}
@media (max-width: 600px) {
  .modal__description {
    font-size: 14px;
  }
}
.modal__window {
  position: relative;
  top: 0;
  transform: translateY(50%);
  background-color: rgb(255, 255, 255);
  margin: 0 50px;
  padding: 50px;
  text-align: center;
}
@media (max-width: 768px) {
  .modal__window {
    margin: 0 30px;
    padding: 100px 20px;
    transform: translateY(30%);
  }
}
@media (max-width: 600px) {
  .modal__window {
    margin: 0;
  }
}

.close-button {
  position: absolute;
  pointer-events: auto;
  right: 30px;
  top: 30px;
  border: none;
  background-color: transparent;
}

.reset-button {
  display: none;
  padding: 5px 20px;
  border: none;
  margin: 30px auto 0 auto;
}
.reset-button--active {
  display: block;
}

.section {
  margin: 40px;
  display: flex;
  justify-content: space-between;
  gap: 50px;
}
.section__image-container {
  max-width: 505px;
}
.section__image {
  max-width: 100%;
  height: auto;
}
@media (max-width: 768px) {
  .section {
    flex-direction: column-reverse;
  }
}

.description {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-evenly;
  gap: 20px;
}
.description__title {
  font-size: 42px;
}
@media (max-width: 600px) {
  .description__title {
    font-size: 32px;
  }
}
.description__paragraph {
  font-size: 16px;
}
@media (max-width: 600px) {
  .description__paragraph {
    font-size: 14px;
  }
}

.button-purple {
  padding: 5px 20px;
  border: none;
  border-radius: 20px;
  background-color: rgb(37, 10, 37);
  color: rgb(255, 255, 255);
  font-weight: 700;
  transition: all 0.4s;
}
.button-purple:hover {
  background-color: rgb(66, 2, 66);
  transform: scale(1.03);
}

body {
  font-family: "Roboto", sans-serif;
}

.wrapper {
  max-width: 1440px;
  margin: 0 auto;
}